.svench-anchor:focus {
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  position: relative;

  & .svench-anchor {
    font-size: inherit;
    position: relative;
  }

  & .svench-anchor {
    visibility: hidden;
    & .icon-link {
      font-size: 100%;
      line-height: 1;
      margin: 0 0.25em;
      font-weight: 700;
      &:before {
        content: '#';
      }
    }
  }
  &:hover .svench-anchor {
    opacity: 1;
    visibility: visible;
  }
}

